import { h } from "@stencil/core"
import { JSXBase } from "@stencil/core/internal"
import kebabCase from "lodash-es/kebabCase"
import { ItemInternal } from "./types"
import { objectMap } from "../../core/helpers/immutability-helpers"
import { ArrowPosition } from "../accordion/types"

type ExternalLinkClickHandler = (e: MouseEvent, item: ItemInternal) => void

type AccordionStyles = {
  accordion: Record<string, string>
  header: Record<string, string>
  arrow: Record<string, string>
  body: Record<string, string>
}

let onExternalLinkClickHandler: ExternalLinkClickHandler = () => undefined

export function setOnExternalLinkClickHandler(fn: ExternalLinkClickHandler) {
  onExternalLinkClickHandler = fn
}

/**
 * Checks if an item is an external link (a.k.a. a "real" link):
 * Basically, an item without children is considered an external link.
 * However, it could have children that are all hidden (even if it's just one),
 * which would lead to treating the link like one without children (e.g. a
 * deeply nested page that does not appear in the menu, but in the breadcrumbs,
 * just like the "Samsung > Samsung Galaxy Watch 4" example).
 * Additionally, also check for href, because a link that simply opens an
 * sdx-header-menu, is technically also a link (but not an external one).
 * @param item Item to check.
 */
export function isExternalLink(item: ItemInternal): boolean {
  const children = getVisibleItems(item.children)
  return !!item.href && (!children.length || !!item.hasLeftHandMenu)
}

export function getAccordionStyles(): AccordionStyles {
  return {
    accordion: {
      border: "none",
    },
    header: {
      fontSize: "inherit",
      fontWeight: "inherit",
      lineHeight: "inherit",
      padding: "0",
    },
    arrow: {
      background: "#015", // $color-navy
    },
    body: {
      marginLeft: "8px", // $baseline
      marginRight: "0",
      marginBottom: "0",
    },
  }
}

export function getMaxDepth(items: ItemInternal[]): number {
  const itemsDepths = items
    .filter((item) => Number(item.depth))
    .map((item) => item.depth as number)

  return Math.max(...itemsDepths)
}

export function getItemClassNames(
  item: ItemInternal,
  allItems: ItemInternal[],
  activeItems: ItemInternal[],
  maxAllowedDepth = 0,
  depth = 0
) {
  const maxDepth = getMaxDepth(allItems)
  const maxDepthEffective =
    maxDepth < maxAllowedDepth ? maxDepth : maxAllowedDepth

  return {
    item: true,
    leaf: !getVisibleItems(item.children).length || !!item.hasLeftHandMenu,
    active: activeItems.includes(item),
    deepest: depth === maxDepthEffective,
  }
}

export function createLink(
  item: ItemInternal,
  props: JSXBase.AnchorHTMLAttributes<HTMLAnchorElement> = {},
  ref: (el?: HTMLSpanElement) => void = () => undefined
): Element {
  let label = item.label

  if (item.slot && !item.showLabelOnDesktop) {
    label = undefined
  }

  return (
    <a
      onClick={(e) => {
        if (isExternalLink(item)) {
          onExternalLinkClickHandler(e, item)
        }
      }}
      href={item.href}
      class="link"
      rel={item.rel}
      aria-current={item.active ? "location" : false}
      // Apple VoiceOver do not recognise elements without visible text or "aria-label",
      // so try to set one of these alternative texts:
      aria-label={item.ariaLabel || item.label || item.iconName}
      aria-expanded={item.ariaExpanded}
      {...objectMap(item.dataset, (key, value) => ({
        [`data-${kebabCase(key)}`]: value,
      }))}
      {...props}
    >
      {item.content ? (
        item.content
      ) : item.iconName ? (
        <span class="icon-label-wrapper">
          <sdx-icon
            size={item.iconSize}
            iconName={item.iconName}
            class={{ icon: true, [item.iconName]: true }}
          />

          {label && (
            <span class="label" ref={ref}>
              {label}
            </span>
          )}

          {item.badge && <span class="badge">{item.badge}</span>}
        </span>
      ) : (
        <span>{label}</span>
      )}
    </a>
  )
}

export function getVisibleItems(items: ItemInternal[] = []): ItemInternal[] {
  return items.filter((child) => !child.hidden)
}

export function createItemForAccordion(
  item: ItemInternal,
  allItems: ItemInternal[],
  activeItems: ItemInternal[],
  arrowPosition: ArrowPosition,
  styles: Partial<AccordionStyles>,
  maxAllowedDepth = Infinity,
  skipLeftHandMenu = false,
  depth = 0
): Element | undefined {
  if (item.hidden) {
    return
  }

  const { accordion, header, arrow, body } = getAccordionStyles()
  const children = getVisibleItems(item.children)

  depth = depth + 1

  return (
    <li
      class={getItemClassNames(
        item,
        allItems,
        activeItems,
        maxAllowedDepth,
        depth
      )}
    >
      {children.length &&
      depth < maxAllowedDepth &&
      !(item.hasLeftHandMenu && skipLeftHandMenu) ? (
        <sdx-accordion
          arrowPosition={arrowPosition}
          componentStyle={{ ...accordion, ...styles.accordion }}
        >
          <sdx-accordion-item open={activeItems.includes(item)}>
            <sdx-accordion-item-header
              buttonStyle={{ ...header, ...styles.header }}
              arrowStyle={{ ...arrow, ...styles.arrow }}
            >
              {createLink(item, { href: "javascript:;" })}
            </sdx-accordion-item-header>

            <sdx-accordion-item-body
              componentStyle={{ ...body, ...styles.body }}
            >
              <ul class="ul vertical">
                {children.map((item) =>
                  createItemForAccordion(
                    item,
                    allItems,
                    activeItems,
                    arrowPosition,
                    styles,
                    maxAllowedDepth,
                    skipLeftHandMenu,
                    depth
                  )
                )}
              </ul>
            </sdx-accordion-item-body>
          </sdx-accordion-item>
        </sdx-accordion>
      ) : (
        createLink(item)
      )}
    </li>
  )
}
