type Breakpoints = {
  xs: 0
  sm: 480
  md: 768
  lg: 1024
  xl: 1280
  ul: 1440
}

export const breakpoints: Breakpoints = {
  xs: 0,
  sm: 480,
  md: 768,
  lg: 1024,
  xl: 1280,
  ul: 1440,
}

export function bp(breakpoint: keyof Breakpoints) {
  return window.innerWidth >= breakpoints[breakpoint]
}
