@import "stylesheets/sdx/typography/typography";
@import "stylesheets/sdx/link/link";
@import "stylesheets/sdx/list/list";

$space-between-links: $baseline * 1.5;
$space-between-items-vertical: 5px;

// Brand decision:
// All links in the header are navy (apart from the secondary, tertiary and
// slot ones) and only the active link is underlined.
a.link {
  color: $color-navy;
  text-decoration: none;

  // Don't break at chevron or icons
  white-space: nowrap;
}

.container {
  // Reflect behavior of .container-page (in _grid.scss).
  // _grid.scss can't be imported because of math.div() errors in
  // @stencil/sass 1.4.1
  @include make-container-fluid(xs);
  @include make-container-fixed(ul);

  display: flex;
}

.push-right {
  margin-right: auto;
}

.icon-label-wrapper {
  @include badge-top-right;
  display: flex;
  align-items: center;
  gap: 6px; // 6px fine tuning
}

.initials {
  background: $color-navy;
  height: $baseline-3;
  width: $baseline-3;
  border-radius: 100%;
  color: $color-white;
  font-size: 12px;
  display: flex;
  justify-content: center;
  align-items: center;
}

// Hide icons on these levels (except for the present icon, see comment below)
.primary,
.main {
  &.ul {
    a.link {
      &[href]:hover {
        .icon {
          // This is only used for promotions on portal and is approved by Brand.
          &.icon-214-present,
          &.icon-present {
            background: $color-orchid--active;
          }
        }
      }
    }

    .icon {
      display: none;

      // This is only used for promotions on portal and is approved by Brand.
      &.icon-214-present,
      &.icon-present {
        display: inline-block;
        transition: background 200ms $standard-easing;
        background: $color-orchid;
        color: white;
        font-size: 16px;
        border-radius: 50%;
        height: $baseline-3;
        width: $baseline-3;
        text-align: center;
      }
    }
  }
}

.ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  display: flex;

  .item {
    display: flex;

    &.active.leaf {
      > a.link {
        text-decoration: underline;
      }
    }

    a.link {
      display: flex;
      align-items: center;
      flex-grow: 1; // increase touch target size
    }
  }

  &.horizontal {
    &:not(:first-of-type) {
      margin-left: $space-between-links;
    }

    &:not(:last-of-type):not(.last-of-type) {
      margin-right: $space-between-links;
    }

    > .item {
      &:not(:first-of-type) {
        margin-left: $space-between-links;
      }

      &:not(:last-of-type):not(.last-of-type) {
        margin-right: $space-between-links;
      }
    }
  }

  &.vertical {
    flex-flow: column;

    > .item {
      margin: $space-between-items-vertical 0;
    }
  }

  &.primary {
    .item {
      &.active {
        .toggle {
          a.link {
            text-decoration: underline;
          }
        }
      }
    }
  }

  &.secondary,
  &.tertiary {
    a.link:not(:hover) {
      color: $color-gray;
    }

    // Arrow
    .toggle {
      a.link:not(:hover) {
        &::before,
        &::after {
          background: $color-gray;
        }
      }
    }
  }

  &.accordion {
    &:not(:first-of-type) {
      padding-top: $baseline;
    }

    &:not(:last-of-type) {
      padding-bottom: $baseline;
      border-bottom: 1px solid $color-aluminium;
    }

    &.main,
    &.left-hand-menu {
      @include font-semi-bold;

      .item {
        &.deepest {
          a.link {
            @include font-semi-light; // overwrite font-semi-bold
          }
        }
      }
    }

    sdx-accordion {
      width: 100%; // ensure chevrons appear at the end

      sdx-accordion-item-body {
        > .ul {
          margin: $space-between-items-vertical 0;

          > .item {
            &:last-of-type {
              margin-bottom: 0;
            }
          }
        }
      }
    }
  }
}

.component {
  @include font-semi-light;
  font-size: 16px;

  &.desktop {
    sdx-header-menu {
      .content {
        max-height: calc(
          var(--sdx-dialog-window-inner-height, 100vh) - #{$header-height-desktop +
            $baseline-2}
        );
      }
    }
  }

  &.mobile {
    sdx-header-menu {
      position: static; // make .content stick to browser window

      .content {
        max-height: calc(
          var(--sdx-dialog-window-inner-height, 100vh) - #{$header-height-mobile +
            $baseline-2}
        );
        margin: 0;
      }
    }
  }

  .wrapper {
    display: flex;
    flex-direction: column;

    // Overlap sdx-backdrop
    position: relative;
    background: $color-white;

    &.meta {
      z-index: $zindex-navigation; // overlap .main and .breadcrumb-list
    }

    &.main {
      // overlap .breadcrumb-list
      border-bottom: 1px solid $color-aluminium;
      z-index: $zindex-navigation - 1; // overlap .breadcrumb-list
      top: 0;
    }

    &.breadcrumb-list {
      z-index: $zindex-navigation - 2;
    }
  }

  .header {
    .meta {
      > .container {
        height: $header-level0-height;
      }
    }

    .main {
      > .container {
        height: $header-level1-height - 1px; // -1px border

        > .ul {
          // Underline active items
          .item {
            &.active {
              .toggle {
                a.link {
                  text-decoration: underline;
                }
              }
            }
          }

          &:not(:first-of-type) {
            margin-left: $baseline-2;
          }

          &:not(:last-of-type):not(.last-of-type) {
            margin-right: $baseline-2;
          }

          &.logo {
            margin-left: -18px;

            .img {
              @include logo-lifeform;
              display: inline-block;
            }
          }

          &.main {
            .toggle {
              @include font-semi-bold;
              font-size: 20px;
            }
          }
        }
      }
    }

    .breadcrumb-list {
      > .container {
        font-size: 14px;

        .ul {
          // Add the height to the .ul instead of to the .container to ensure
          // that sdx-scroll-view doesn't override it (being its slot)
          height: $header-breadcrumb-height;
        }

        .item {
          margin: 0;

          &:first-of-type {
            .label {
              display: none; // don't display the label on the home icon
            }
          }

          &:not(:first-of-type) {
            &::before {
              content: ">";
              align-self: center;
              margin: 0 $baseline;
            }
          }
        }
      }
    }

    .slot-menu-header,
    .slot-menu-content {
      padding: $baseline-2;
    }

    .slot-menu-header {
      display: flex;

      // Align "x" to long titles that break into multiple lines
      align-items: start;

      .title {
        flex: 1;
        margin: 0; // vertically align with close button
      }
    }

    .slot-menu-content {
      a.link {
        color: $color-link;
      }

      .list {
        margin: 0;
      }
    }

    .icon-gateway {
      &::before {
        font-family: $font-family-sdx;
        content: "\e0e5"; // .icon-gateway
      }
    }
  }
}

sdx-button {
  // Align the slot buttons vertically
  display: flex;
  align-items: center;
}

.component.sdx--dark-theme {
  a.link {
    color: $color-gray-dark-theme;

    &:hover {
      color: $color-int-blue2-dark-theme--active;
    }
  }

  .initials {
    background: $color-gray-tint-8-dark-theme;
  }

  // Hide icons on these levels (except for the present icon, see comment below)
  .primary,
  .main {
    &.ul {
      a.link {
        &:hover {
          .icon {
            // This is only used for promotions on portal and is approved by Brand.
            &.icon-214-present,
            &.icon-present {
              background: $color-orchid-tint-2-dark-theme;
            }
          }
        }
      }

      .icon {
        // This is only used for promotions on portal and is approved by Brand.
        &.icon-214-present,
        &.icon-present {
          background: $color-orchid-dark-theme;
        }
      }
    }
  }

  .ul {
    &.secondary,
    &.tertiary {
      a.link:not(:hover) {
        color: $color-int-gray-dark-theme;
      }

      // Arrow
      .toggle {
        a.link:not(:hover) {
          &::before,
          &::after {
            background: $color-int-gray-dark-theme;
          }
        }
      }
    }

    &.accordion {
      &:not(:last-of-type) {
        border-bottom-color: $color-gray-tint-8-dark-theme;
      }
    }
  }

  .wrapper {
    background: $color-dusk-tint-2-dark-theme;

    &.main {
      // overlap .breadcrumb-list
      border-bottom-color: $color-gray-tint-7-dark-theme;
    }

    &.breadcrumb-list {
      background: $color-dusk-dark-theme; // is part of page content > override header color
    }
  }
}
