import {
  Component,
  h,
  Element,
  Prop,
  Watch,
  Event,
  EventEmitter,
  Listen,
  Method,
  Host,
  getAssetPath,
  VNodeData,
} from "@stencil/core"
import {
  Navigation,
  Item,
  Slot,
  Login,
  ItemInternal,
  SlotInternal,
} from "./types"
import {
  createLink,
  createItemForAccordion,
  getItemClassNames,
  getVisibleItems,
  isExternalLink,
  setOnExternalLinkClickHandler,
} from "./helpers"
import {
  disableBodyScroll,
  enableBodyScroll,
  flattenBy,
  getScrollbarWidth,
  parseJson,
  width,
  getAppearance,
  translate,
} from "../../core/helpers/webcomponent-helpers"
import { bp } from "../../core/helpers/breakpoint-helpers"
import { toggle } from "../../core/helpers/immutability-helpers"
import { Display } from "../../core/types/types"

@Component({
  tag: "sdx-header",
  styleUrl: "header.scss",
  assetsDirs: ["assets"],
  shadow: true,
})
export class Header {
  private navigationParsed!: Required<Navigation> // will be set in constructor()
  private indexParsed?: Item
  private loginParsed?: Login
  private slotsParsed: Slot[]

  private display: "desktop" | "mobile" = "desktop"
  private scrollbarWidth = getScrollbarWidth()
  private innerWidth = innerWidth

  private componentEl?: HTMLDivElement

  // Meta level
  private metaWrapperEl?: HTMLDivElement
  private metaContainerEl?: HTMLDivElement

  private primaryListEl?: HTMLUListElement
  private secondaryEl?: HTMLUListElement
  private secondaryListEl?: HTMLUListElement
  private tertiaryListEl?: HTMLUListElement

  // Main level
  private mainWrapperEl?: HTMLDivElement
  private mainContainerEl?: HTMLDivElement
  private swisscomLogoEl?: HTMLUListElement
  private mainListEl?: HTMLUListElement
  private slotsEl?: HTMLUListElement
  private hamburgerEl?: HTMLUListElement
  private slotLabelEls: HTMLSpanElement[] = []
  private pathLogo = "./assets/lifeform-spritesheet@2x.png"
  private pathLogoPride = "./assets/lifeform-pride.png"

  // Breadcrumbs level
  private breadcrumbListWrapperEl?: HTMLDivElement
  private breadcrumbListEl?: HTMLUListElement
  private sdxScrollViewEl?: HTMLSdxScrollViewElement

  private leftHandMenuEl?: HTMLSdxHeaderLeftHandMenuElement
  private leftHandMenuOutletEl?: HTMLElement

  private breadcrumbListScriptEl?: HTMLScriptElement
  private openedHeaderMenuEls: HTMLSdxHeaderMenuElement[] = []

  private allItems!: ItemInternal[] // will be set in constructor()
  private activeItems!: ItemInternal[] // will be set in constructor()
  private breadcrumbList!: ItemInternal[] // will be set in constructor()

  private resizeTimer?: NodeJS.Timer
  private componentDidLoadComplete = false

  private slotsWithSdxHeaderMenuEl = new Map<string, HTMLSdxHeaderMenuElement>()

  @Element() public el!: HTMLSdxHeaderElement

  /**
   * The navigation is divided in 4 categories, each holding a list of items.
   *
   * Primary links link to pages that belong to the same department of the app
   * and don't have an icon. Note that the currently active primary link needs
   * to be set to `active: true`.
   *
   * Secondary links link to related, but independent pages and have an icon.
   * Note that secondary links are less prioritized and will collapse into a
   * dropdown if there's not enough space. They don't need to be set to
   * `active: true` because they're only links to external pages.
   *
   * Tertiary links are exclusively reserved for contact or language selection.
   * Note that the currently selected language does not have to be listed.
   *
   * Main links link to pages inside the app and don't have an icon. They can be
   * nested recursively up to 4 levels. Menus are created automatically.
   * Note that the currently active link has to be set to `active: true`.
   * This will both visualize it properly and display the breadcrumbs.
   *
   * For SEO, a schema.org compliant JSON-LD is automatically generated and
   * appended. The Item's `label` and `href` properties are used for `name` and
   * `item`.
   *
   * If needed, a left hand menu can be added by providing a HTML element like
   * `<div id="sdx-left-hand-menu-outlet"></div>`, as demonstrated on
   * the SDX website (view page source).
   *
   *     type Item {
   *       label?: string;
   *       href?: string;
   *       iconName?: string;
   *       active?: boolean;
   *       hidden?: boolean;
   *       dataset?: Record<string, string>;
   *       children?: Item[];
   *       ariaLabel?: string;
   *       rel?: "nofollow";
   *     }
   */
  @Prop() public navigation: Navigation | string = {}

  /**
   * Link item that will be used for both the Swisscom logo and the home icon
   * in the breadcrumbs. Even though it's not displayed, the Item's `label`
   * property should still be set as it's used to generate the *schema.org*
   * compliant JSON+LD. Links to `/` by default.
   */
  @Prop() public index: Item | string = { href: "/" }

  /**
   * The login area. Can be a slot menu or a link to a login page (depending on
   * whether either `href` or `slot` is set).
   * If a `customer` is given, it turns into the logged-in state. It also
   * renders a logout link when `logout` is set.
   * Example:
   *
   *     <sdx-header login='[{ "slot": "login", "customer": { "fullName": "Sophie Hauser" } }]'>
   *       <div slot="login">Sophie's menu</div>
   *     </sdx-header>
   */
  @Prop() public login?: Login | string

  /**
   * Create custom menu items that will display their corresponding slot
   * provided as follows.
   *
   *     <sdx-header slots='[{ "slot": "my-menu", … }]'>
   *       <div slot="my-menu">My menu content</div>
   *     </sdx-header>
   */
  @Prop() public slots?: Slot[] | string

  /**
   * Disable animations for testing.
   * @private
   */
  @Prop() public animated: boolean = true

  @Watch("navigation")
  public navigationChanged() {
    this.parseNavigationIntoState()
    this.createBreadcrumbListScriptEl()
  }

  @Watch("index")
  public indexChanged() {
    this.indexParsed = parseJson(this.index)
  }

  @Watch("login")
  public loginChanged() {
    this.loginParsed = parseJson(this.login)
  }

  @Watch("slots")
  public slotsChanged() {
    this.slotsParsed = parseJson(this.slots) || []
  }

  /**
   * Emitted whenever the user navigates.
   * It contains the navigation item as `detail.item`.
   * Preventing the default action on the event will cancel the navigate to the item’s URL,
   * e.g. to implement links in SEO-friendly SPAs:
   *
   *     document.querySelector("sdx-header").addEventListener("sdxnavigate", (event) => {
   *       event.preventDefault()
   *     })
   */
  @Event() public sdxnavigate!: EventEmitter<{ item: Item }>

  @Listen("scroll", { target: "window" })
  public onWindowScroll() {
    if (!this.componentDidLoadComplete) {
      return
    }

    this.makeHeaderSticky()
  }

  @Listen("resize", { target: "window" })
  public onWindowResizeDebounced() {
    const innerWidthChanged = innerWidth !== this.innerWidth

    // Ignore changes in height as they're not relevant for the layout
    // calculations. They're fired e.g. by mobiles when hiding the address bar
    // and can lead to unwanted behavior on some mobile browsers (see SDX-1182).
    if (!innerWidthChanged) {
      return
    }

    this.innerWidth = innerWidth

    if (this.resizeTimer) {
      clearTimeout(this.resizeTimer)
    }

    this.resizeTimer = setTimeout(() => {
      this.layout()
    }, 10)
  }

  /**
   * Opens a specific slot previously registered in `slots`.
   * @param slot Name that was used to register the slot.
   */
  @Method()
  public async openSlot(slot: string) {
    const sdxHeaderMenuEl = this.slotsWithSdxHeaderMenuEl.get(slot)
    sdxHeaderMenuEl?.open()
  }

  /**
   * Closes a specific slot previously registered in `slots`.
   * @param slot Name that was used to register the slot.
   */
  @Method()
  public async closeSlot(slot: string) {
    const sdxHeaderMenuEl = this.slotsWithSdxHeaderMenuEl.get(slot)
    sdxHeaderMenuEl?.close()
  }

  constructor() {
    this.indexParsed = parseJson(this.index)
    this.loginParsed = parseJson(this.login)
    this.slotsParsed = parseJson(this.slots) || []

    this.parseNavigationIntoState()
    this.createBreadcrumbListScriptEl()

    this.leftHandMenuOutletEl =
      document.getElementById("sdx-left-hand-menu-outlet") || undefined

    setOnExternalLinkClickHandler((e, item) =>
      this.onExternalLinkClick(e, item)
    )
  }

  public componentDidLoad() {
    this.layout()

    // Scroll breadcrumbs to the end to show the active breadcrumb on small screens
    if (this.breadcrumbListEl && this.sdxScrollViewEl) {
      const { scrollWidth, clientWidth } = this.breadcrumbListEl

      // In Firefox 106, scrollWidth at this point is -1px wrong, which makes
      // the right arrow appear even though it's not necessary. However, since
      // there aren't many Firefox users on mobile, let's leave it like that in
      // hope that Firefox will fix it one day.
      this.sdxScrollViewEl.translateX(scrollWidth - clientWidth, false)
    }

    // Append left hand menu (if there)
    if (this.leftHandMenuOutletEl && this.leftHandMenuEl) {
      this.leftHandMenuOutletEl.append(this.leftHandMenuEl)
    }

    this.componentDidLoadComplete = true
  }

  public componentDidUpdate() {
    // Ensure layout is recalculated because when new slots are added, they
    // might have to be hidden for the current display (e.g. mobile).
    this.layout()
  }

  private headerMenuDisplayChangeCallback(
    el: HTMLSdxHeaderMenuElement,
    display: Display
  ) {
    // Know if menus are currently open
    if (display === "opening" || display === "closed") {
      this.openedHeaderMenuEls = toggle(this.openedHeaderMenuEls, el)

      if (this.openedHeaderMenuEls.length) {
        disableBodyScroll()
      } else {
        enableBodyScroll()
      }
    }
  }

  private getParsedNavigationProp(): Required<Navigation> {
    const parsedNavigation = parseJson<Navigation>(this.navigation) || {}

    // Prevent null pointer references by assigning defaults
    return {
      primary: { children: [] },
      secondary: { children: [] },
      tertiary: { children: [] },
      main: { children: [] },
      ...parsedNavigation,
    }
  }

  private parseNavigationIntoState(): void {
    this.navigationParsed = this.getParsedNavigationProp()

    this.allItems = [
      ...flattenBy(this.navigationParsed.primary, "children"),
      ...flattenBy(this.navigationParsed.secondary, "children"),
      ...flattenBy(this.navigationParsed.tertiary, "children"),
      ...flattenBy(this.navigationParsed.main, "children"),
    ].filter((item) => !item.hidden)

    this.activeItems = [
      ...this.getActiveItems(this.navigationParsed.primary),
      ...this.getActiveItems(this.navigationParsed.main),
    ]

    this.breadcrumbList = [
      { ...this.indexParsed, iconName: "icon-home" },
      ...this.getActiveItems(this.navigationParsed.main),
    ]
  }

  private onExternalLinkClick(e: MouseEvent, item: ItemInternal) {
    // Close menu
    this.openedHeaderMenuEls.forEach((el) => el.close())

    const customEvent = this.sdxnavigate.emit({
      item: {
        // just use these 3 properties, see SDX-1281
        label: item.label,
        href: item.href,
        dataset: item.dataset,
      },
    })

    if (customEvent.defaultPrevented) {
      e.preventDefault()
    }
  }

  private createBreadcrumbListScriptEl() {
    // Delete previous if there
    this.breadcrumbListScriptEl?.remove()

    if (!this.hasBreadcrumbs()) {
      return
    }

    this.breadcrumbListScriptEl = document.createElement("script")
    this.breadcrumbListScriptEl.type = "application/ld+json"

    this.breadcrumbListScriptEl.innerHTML = JSON.stringify(
      this.createBreadcrumbListSchema()
    )

    this.el.after(this.breadcrumbListScriptEl)
  }

  private createListForDesktop(
    key: keyof Navigation,
    ref: (el?: HTMLUListElement) => void,
    content?: VNodeData
  ): Element | undefined {
    const items = getVisibleItems(this.navigationParsed[key].children)

    if (!items.length) {
      return
    }

    return (
      <ul
        class={{ [key]: true, "ul horizontal": true }}
        role="navigation"
        aria-label={key}
        ref={ref}
      >
        {content ? content : items.map((item) => this.createItemForMeta(item))}
      </ul>
    )
  }

  private createListForMobile(key: keyof Navigation, arrowBackground?: string) {
    const items = getVisibleItems(this.navigationParsed[key].children)

    if (!items.length) {
      return
    }

    return (
      <ul
        class={{ [key]: true, "ul vertical accordion": true }}
        role="navigation"
        aria-label={key}
      >
        {items.map((item) =>
          createItemForAccordion(
            item,
            this.allItems,
            this.activeItems,
            "right",
            {
              ...(arrowBackground
                ? {
                    arrow: {
                      background: arrowBackground,
                    },
                  }
                : undefined),
            }
          )
        )}
      </ul>
    )
  }

  private getActiveItems(
    item: ItemInternal,
    accumulator: ItemInternal[] = [],
    depth = 0
  ): ItemInternal[] {
    if (item.children?.length) {
      depth = depth + 1

      item.children.forEach((_item) => {
        _item.parent = item
        _item.depth = depth
        this.getActiveItems(_item, accumulator, depth)
      })
    } else {
      // ...reached leaf, bubble up and collect active items
      let currentItem = item
      let hasActiveChildren = false

      while (currentItem.parent) {
        if (currentItem.active || hasActiveChildren) {
          if (!accumulator.includes(currentItem)) {
            accumulator.unshift(currentItem)
          }

          hasActiveChildren = true
        }

        currentItem = currentItem.parent
      }
    }

    return accumulator
  }

  private createBreadcrumbListSchema() {
    return {
      "@context": "https://schema.org",
      "@type": "BreadcrumbList",
      itemListElement: this.breadcrumbList
        .filter((item) => item.href || item.active) // skip items without a real link (except the active one, which doesn’t need a link)
        .map((item, i) => ({
          "@type": "ListItem",
          position: i + 1,
          name: item.label,
          item: item.active ? undefined : item.href,
        })),
    }
  }

  private hasBreadcrumbs(): boolean {
    // Don't show breadcrumbs when "home" is the only item
    return this.breadcrumbList.length > 1
  }

  private hasLeftHandMenu(): boolean {
    return !!(this.getLeftHandMenuItems().length && this.leftHandMenuOutletEl)
  }

  private getLeftHandMenuItems(): ItemInternal[] {
    const leftHandMenuItemsPosition = this.breadcrumbList.findIndex(
      (item) => item.hasLeftHandMenu
    )

    return this.breadcrumbList[leftHandMenuItemsPosition]?.children || []
  }

  private layout(): void {
    // Clean up from previous layout()
    this.componentEl!.classList.remove("desktop")
    this.componentEl!.classList.remove("mobile")

    // Strategy is desktop first, then switch to mobile if needed
    this.componentEl!.classList.add("desktop")
    this.componentEl!.style.display = "block"

    this.metaWrapperEl!.style.display = "flex"
    this.hamburgerEl!.style.display = "none"

    if (this.mainListEl) {
      this.mainListEl.style.display = "flex"
    }

    if (this.leftHandMenuOutletEl) {
      this.leftHandMenuOutletEl.style.display = "block"
    }

    this.showSecondaryIfEnoughSpace()
    this.showSlotLabelsIfEnoughSpace()
    this.setDisplayToDesktopOrMobile()
    this.makeHeaderSticky()
    this.hidePartsOnMobile()

    // Now that the layout is fully calculated, apply the corresponding height
    // variable.
    this.el.style.height = `var(--sdx-header-height-${this.display}${
      this.hasBreadcrumbs() ? "-with-breadcrumbs" : ""
    })`
  }

  private showSecondaryIfEnoughSpace(): void {
    if (this.secondaryEl) {
      this.secondaryEl.style.display = "none"
    }

    if (this.secondaryListEl) {
      this.secondaryListEl.style.display = "flex"
    }

    const unusedWidth =
      width(this.metaContainerEl) -
      width(this.primaryListEl, true) -
      width(this.tertiaryListEl, true)

    if (width(this.secondaryListEl, true) > unusedWidth) {
      if (this.secondaryEl) {
        this.secondaryEl.style.display = "flex"
      }

      if (this.secondaryListEl) {
        this.secondaryListEl.style.display = "none"
      }
    }
  }

  private showSlotLabelsIfEnoughSpace(): void {
    this.slotLabelEls.forEach((el) => (el.style.display = "block"))

    const unusedWidth =
      width(this.mainContainerEl) -
      width(this.swisscomLogoEl, true) -
      width(this.mainListEl, true) -
      width(this.slotsEl, true)

    if (unusedWidth < 0) {
      this.slotLabelEls.forEach((el) => (el.style.display = "none"))
    }
  }

  private setDisplayToDesktopOrMobile(): void {
    const metaElContentWidth =
      width(this.primaryListEl, true) +
      width(this.secondaryListEl, true) +
      width(this.secondaryEl, true) +
      width(this.tertiaryListEl, true)

    const mainElContentWidth =
      width(this.swisscomLogoEl, true) +
      width(this.mainListEl, true) +
      width(this.slotsEl, true)

    const hasEnoughSpaceForMetaEl =
      width(this.metaContainerEl) >= metaElContentWidth
    const hasEnoughSpaceForMainEl =
      width(this.mainContainerEl) >= mainElContentWidth

    this.display =
      hasEnoughSpaceForMetaEl && hasEnoughSpaceForMainEl && bp("lg")
        ? "desktop"
        : "mobile"
  }

  private makeHeaderSticky(): void {
    // Unfortunately, "sticky" can't be use for the whole length of the page,
    // because it only works within its container. It's only being used here
    // for the nice graphical effect when starting to scroll.
    const stickyUntil =
      this.display === "desktop"
        ? 40 // $header-level0-height
        : 0

    const isSticky = scrollY < stickyUntil

    // Reset previously applied styles
    this.el.style.width = ""
    this.metaWrapperEl!.style.paddingRight = ""
    this.mainWrapperEl!.style.width = ""
    this.mainWrapperEl!.style.paddingRight = ""
    this.breadcrumbListWrapperEl!.style.paddingRight = ""

    if (isSticky) {
      this.mainWrapperEl!.style.position = "sticky"
      this.breadcrumbListWrapperEl!.style.top = ""

      if (this.openedHeaderMenuEls.length) {
        // Reserve space for potential scrollbar
        this.el.style.width = "100vw"
        this.metaWrapperEl!.style.paddingRight = `${this.scrollbarWidth}px`
        this.mainWrapperEl!.style.paddingRight = `${this.scrollbarWidth}px`
        this.breadcrumbListWrapperEl!.style.paddingRight = `${this.scrollbarWidth}px`
      }
    } else {
      this.mainWrapperEl!.style.position = "fixed"
      this.breadcrumbListWrapperEl!.style.top = "72px" // $header-sticky-height

      // Reserve space for potential scrollbar
      this.mainWrapperEl!.style.width = "100vw"
      this.metaWrapperEl!.style.paddingRight = `${this.scrollbarWidth}px`
      this.mainWrapperEl!.style.paddingRight = `${this.scrollbarWidth}px`
      this.breadcrumbListWrapperEl!.style.paddingRight = `${this.scrollbarWidth}px`
    }
  }

  private hidePartsOnMobile(): void {
    if (this.display === "desktop") {
      return
    }

    this.componentEl!.classList.remove("desktop")
    this.componentEl!.classList.add("mobile")
    this.metaWrapperEl!.style.display = "none"
    this.slotLabelEls.forEach((el) => (el.style.display = "none"))
    this.hamburgerEl!.style.display = "flex"

    if (this.mainListEl) {
      this.mainListEl.style.display = "none"
    }

    if (this.leftHandMenuOutletEl) {
      this.leftHandMenuOutletEl.style.display = "none"
    }
  }

  private createItemForMeta(item: ItemInternal): Element {
    const children = getVisibleItems(item.children)

    return (
      <li class={getItemClassNames(item, this.allItems, this.activeItems, 2)}>
        <sdx-header-menu
          level="meta"
          animated={this.animated}
          displayChangeCallback={(el, display) =>
            this.headerMenuDisplayChangeCallback(el, display)
          }
        >
          {createLink(item, {
            href: item.href || "javascript:;",
            slot: "toggle",
          })}

          {!!children.length && (
            <div slot="content">
              <ul class="ul vertical">
                {children.map((item) => (
                  <li
                    class={getItemClassNames(
                      item,
                      this.allItems,
                      this.activeItems
                    )}
                  >
                    {createLink(item)}
                  </li>
                ))}
              </ul>
            </div>
          )}
        </sdx-header-menu>
      </li>
    )
  }

  private createItemForMain(item: ItemInternal): Element {
    const children = getVisibleItems(item.children)
    const _isExternalLink = isExternalLink(item)

    return (
      <li
        class={getItemClassNames(item, this.allItems, this.activeItems, 2)}
        ref={(el) => (item.el = el)}
      >
        <sdx-header-menu
          level="main"
          layout="fixed-left"
          animated={this.animated}
          displayChangeCallback={(el, display) =>
            this.headerMenuDisplayChangeCallback(el, display)
          }
        >
          {createLink(item, {
            href: _isExternalLink ? item.href : "javascript:;",
            slot: "toggle",
          })}

          {!_isExternalLink && (
            <ul class="ul vertical accordion main" slot="content">
              {children.map((item) =>
                createItemForAccordion(
                  item,
                  this.allItems,
                  this.activeItems,
                  "right",
                  getAppearance(this.el) === "sdx--light-theme"
                    ? {}
                    : {
                        arrow: {
                          background: "#e6e6e6" /* $color-gray-dark-theme */,
                        },
                      },
                  2,
                  true
                )
              )}
            </ul>
          )}
        </sdx-header-menu>
      </li>
    )
  }

  private createItemForSlot(slot: SlotInternal, content?: VNodeData): Element {
    return slot.slot ? (
      <li class="item">
        <sdx-header-menu
          level="main"
          layout="fixed-right"
          class="slot"
          animated={this.animated}
          width={slot.menuWidth}
          scrollable={slot.scrollable}
          displayChangeCallback={(el, display) =>
            this.headerMenuDisplayChangeCallback(el, display)
          }
          ref={(ref) => this.slotsWithSdxHeaderMenuEl.set(slot.slot!, ref!)}
        >
          {this.createSlotToggle(slot)}

          <div slot="content">
            <div class="slot-menu-header">
              <div class="h5 title">{slot.label}</div>

              <sdx-button
                theme="transparent"
                iconName="icon-close"
                iconSize={2}
                onClick={() =>
                  this.slotsWithSdxHeaderMenuEl.get(slot.slot!)!.toggle()
                }
              />
            </div>

            <slot name={slot.slot} />

            {content && <div class="slot-menu-content">{content}</div>}
          </div>
        </sdx-header-menu>
      </li>
    ) : (
      <li class="item leaf">{this.createSlotToggle(slot)}</li>
    )
  }

  private createSlotToggle(slot: Slot): Element {
    return createLink(
      {
        ...slot,
        iconName: slot.iconName,
        iconSize: 2,
      },
      {
        slot: "toggle",
        href: slot.href || "javascript:;",
      },
      (el) => {
        if (el) {
          this.slotLabelEls.push(el)
        }
      }
    )
  }

  private createInitials(fullName: string | undefined): string {
    if (!fullName) {
      return ""
    }

    return fullName
      .split(/\s+/)
      .filter(Boolean)
      .map((part) => part[0]!.toLocaleUpperCase())
      .join("")
  }

  private getComponentClassNames() {
    return {
      [getAppearance(this.el)]: true,
      component: true,
    }
  }

  public render() {
    const { secondary, main } = this.navigationParsed

    // Clear previous entries
    this.slotsWithSdxHeaderMenuEl.clear()
    this.slotLabelEls = []

    // Initials with backwards compatibility
    const initials =
      this.createInitials(this.loginParsed?.customer?.fullName) ||
      this.loginParsed?.initials

    // If there are initials, consider the user as logged in
    const isLoggedIn = !!initials

    return (
      <Host>
        <div
          class={this.getComponentClassNames()}
          ref={(el) => (this.componentEl = el)}
          style={{
            // Initially hide the whole component (not to be confused with
            // _reserve-height.scss that sets display: block; to prevent CLS).
            // Otherwise *all* its content is briefly shown (only visible using
            // Network throttling) before layout() has completed.
            // After that, it will be set to visible.
            display: "none",
          }}
        >
          <div class="header">
            <div class="wrapper meta" ref={(el) => (this.metaWrapperEl = el)}>
              <div class="container" ref={(el) => (this.metaContainerEl = el)}>
                {this.createListForDesktop(
                  "primary",
                  (el) => (this.primaryListEl = el)
                )}

                <div class="push-right"></div>

                {this.createListForDesktop(
                  "secondary",
                  (el) => (this.secondaryEl = el),
                  this.createItemForMeta(secondary)
                )}

                {this.createListForDesktop(
                  "secondary",
                  (el) => (this.secondaryListEl = el)
                )}

                {this.createListForDesktop(
                  "tertiary",
                  (el) => (this.tertiaryListEl = el)
                )}
              </div>
            </div>

            <div class="wrapper main" ref={(el) => (this.mainWrapperEl = el)}>
              <div class="container" ref={(el) => (this.mainContainerEl = el)}>
                <ul class="ul logo" ref={(el) => (this.swisscomLogoEl = el)}>
                  <li class="item leaf">
                    {createLink({
                      ...this.indexParsed,
                      ariaLabel: "swisscom home",
                      content: (
                        <span
                          class="img"
                          style={{
                            backgroundImage: `url(${getAssetPath(
                              new Date().getMonth() === 5
                                ? this.pathLogoPride
                                : this.pathLogo
                            )})`,
                          }}
                        />
                      ),
                    })}
                  </li>
                </ul>

                {this.createListForDesktop(
                  "main",
                  (el) => (this.mainListEl = el),
                  getVisibleItems(main.children).map((item) =>
                    this.createItemForMain(item)
                  )
                )}

                <div class="push-right"></div>

                {(!!this.slotsParsed.length || !!this.loginParsed) && (
                  // Add .last-of-type because the next list is hidden
                  <ul
                    key="slots" // key because conditional ref
                    class="ul horizontal last-of-type"
                    ref={(el) => (this.slotsEl = el)}
                  >
                    {!!this.slotsParsed.length &&
                      this.slotsParsed.map((slot) =>
                        this.createItemForSlot(slot)
                      )}

                    {!!this.loginParsed &&
                      this.createItemForSlot(
                        {
                          ...this.loginParsed,
                          label: isLoggedIn
                            ? `${translate("Hello")} ${
                                this.loginParsed.customer?.fullName
                              }`
                            : translate("Login"),
                          iconName: "icon-account",
                          showLabelOnDesktop: true,
                          content: isLoggedIn ? (
                            <span class="initials">{initials}</span>
                          ) : undefined,
                        },
                        isLoggedIn && !!this.loginParsed.logout && (
                          <ul class="list single-line">
                            <li class="icon-gateway">
                              {createLink({
                                ...this.loginParsed.logout,
                                label: translate("Logout"),
                              })}
                            </li>
                          </ul>
                        )
                      )}
                  </ul>
                )}

                <ul
                  class="ul horizontal hamburger"
                  ref={(el) => (this.hamburgerEl = el)}
                >
                  <li class="item">
                    <sdx-header-menu
                      level="main"
                      layout="fixed-right"
                      animated={this.animated}
                      displayChangeCallback={(el, display) =>
                        this.headerMenuDisplayChangeCallback(el, display)
                      }
                    >
                      {createLink(
                        {
                          iconName: "icon-menu",
                          iconSize: 3,
                          ariaLabel: "open the menu",
                          ariaExpanded: "false",
                        },
                        { href: "javascript:;", slot: "toggle" }
                      )}

                      <div slot="content">
                        {this.createListForMobile(
                          "main",
                          getAppearance(this.el) === "sdx--light-theme"
                            ? undefined
                            : "#e6e6e6" /* $color-gray-dark-theme */
                        )}

                        {this.createListForMobile(
                          "primary",
                          getAppearance(this.el) === "sdx--light-theme"
                            ? undefined
                            : "#e6e6e6" /* $color-gray-dark-theme */
                        )}

                        {this.createListForMobile(
                          "secondary",
                          getAppearance(this.el) === "sdx--light-theme"
                            ? "#666" /* $color-gray-tint-4 */
                            : "#adadad" /* $color-gray-tint-5-dark-theme */
                        )}

                        {this.createListForMobile(
                          "tertiary",
                          getAppearance(this.el) === "sdx--light-theme"
                            ? "#666" /* $color-gray-tint-4 */
                            : "#adadad" /* $color-gray-tint-5-dark-theme */
                        )}
                      </div>
                    </sdx-header-menu>
                  </li>
                </ul>
              </div>
            </div>

            <div
              class="wrapper breadcrumb-list"
              ref={(el) => (this.breadcrumbListWrapperEl = el)}
            >
              {this.hasBreadcrumbs() && (
                <nav class="container" aria-label="breadcrumb">
                  <sdx-scroll-view
                    arrowsHidden={true}
                    ref={(el) => (this.sdxScrollViewEl = el)}
                  >
                    <ul
                      class="ul horizontal"
                      ref={(el) => (this.breadcrumbListEl = el)}
                    >
                      {this.breadcrumbList.map((item, i) => (
                        <li class="item">
                          {createLink({
                            ...item,
                            href:
                              i === this.breadcrumbList.length - 1
                                ? undefined // don't link last (= active page)
                                : item.href,
                          })}
                        </li>
                      ))}
                    </ul>
                  </sdx-scroll-view>
                </nav>
              )}
            </div>
          </div>
        </div>

        {this.hasLeftHandMenu() && (
          <sdx-header-left-hand-menu
            items={this.getLeftHandMenuItems()}
            allItems={this.allItems}
            activeItems={this.activeItems}
            ref={(el) => (this.leftHandMenuEl = el)}
          />
        )}
      </Host>
    )
  }
}
