import {
  Component,
  h,
  Element,
  State,
  Prop,
  Watch,
  Listen,
  Host,
  Method,
} from "@stencil/core"
import anime from "animejs"
import {
  hasSlot,
  setCssVariableWindowInnerHeight,
  getAppearance,
  appendToOverlayOutlet,
} from "../../../core/helpers/webcomponent-helpers"
import { Display } from "../../../core/types/types"

/**
 * @private
 */
@Component({
  tag: "sdx-header-menu",
  styleUrl: "header-menu.scss",
  shadow: false, // needed for coloring the .arrow
})
export class HeaderMenu {
  private animationDuration = 200
  private toggleEl?: HTMLSpanElement
  private contentEl?: HTMLDivElement
  private slotContainerEl?: HTMLElement
  private sdxBackdropEl?: HTMLSdxBackdropElement
  private trapFocusEl?: HTMLSdxTrapFocusElement

  @Element() public el!: HTMLSdxHeaderMenuElement

  @State() private display: Display = "closed"

  @Prop() public level: "meta" | "main" | "breadcrumbs" = "meta"
  @Prop() public layout: "auto" | "fixed-left" | "fixed-right" = "auto"
  @Prop() public width?: string
  @Prop() public scrollable = true

  @Prop() public displayChangeCallback: (
    el: HTMLSdxHeaderMenuElement,
    display: Display
  ) => void = () => undefined

  /**
   * Disable animations for testing.
   * @private
   */
  @Prop() public animated: boolean = true

  @Watch("display")
  public displayChanged() {
    if (this.display === "opening") {
      setCssVariableWindowInnerHeight()
    }

    this.displayChangeCallback(this.el, this.display)
  }

  @Listen("resize", { target: "window" })
  public onWindowResize() {
    if (this.display === "open") {
      setCssVariableWindowInnerHeight()
    }
  }

  @Method()
  public async toggle() {
    if (this.display === "open" || this.display === "opening") {
      this.close()
    } else {
      this.open()
    }
  }

  @Method()
  public async open() {
    if (this.display === "open" || this.display === "opening") {
      return
    }

    const contentEl = this.contentEl!
    const slotContainerEl = this.slotContainerEl!
    const sdxBackdropEl = this.sdxBackdropEl!

    this.display = "opening"

    appendToOverlayOutlet(sdxBackdropEl, document.body, this.el)

    anime.set(sdxBackdropEl, { display: "block" })

    anime.set(contentEl, {
      transformOrigin: "50% 0",
      display: "block",
      scaleY: 0,
      opacity: 0.2,
    })

    anime.set(slotContainerEl, { opacity: 0 })

    anime
      .timeline({
        duration: this.animated ? this.animationDuration : 0,
        easing: "cubicBezier(0.550, 0.085, 0.320, 1)",
        complete: () => {
          this.trapFocusEl?.doFocus()
          this.display = "open"
        },
      })
      .add({
        targets: sdxBackdropEl,
        backdropFilter: [
          "blur(0px) brightness(100%)",
          "blur(4px) brightness(50%)",
        ],
      })
      .add(
        {
          targets: contentEl,
          opacity: 1,
          scaleY: 1,
        },
        0
      )
      .add({
        targets: slotContainerEl,
        opacity: 1,
      })
  }

  @Method()
  public async close() {
    if (this.display === "closed" || this.display === "closing") {
      return
    }

    const contentEl = this.contentEl!
    const sdxBackdropEl = this.sdxBackdropEl!

    this.display = "closing"

    anime
      .timeline({
        duration: this.animated ? this.animationDuration : 0,
        easing: "cubicBezier(0.550, 0.085, 0.320, 1)",
        complete: () => {
          anime.set([sdxBackdropEl, contentEl], { display: "none" })

          appendToOverlayOutlet(sdxBackdropEl, this.el, this.el)

          // Set focus to the toggle element
          const toggleSlotEl = this.el.querySelector(
            "[slot='toggle']"
          ) as HTMLElement

          toggleSlotEl.focus()

          this.display = "closed"
        },
      })
      .add({
        targets: sdxBackdropEl,
        backdropFilter: "blur(0px) brightness(100%)",
      })
      .add(
        {
          targets: contentEl,
          opacity: 0.2,
          scaleY: 0,
        },
        0
      )
  }

  /**
   * @private
   * Calls close(), but only when the component is allowed to be closed.
   */
  @Method()
  public async requestToClose(
    e: KeyboardEvent | MouseEvent | TouchEvent
  ): Promise<void> {
    if (!(this.toggleEl && this.contentEl && this.display === "open")) {
      return
    }

    if (e instanceof KeyboardEvent) {
      if (e.key === "Escape") {
        this.close()
      }

      return
    }

    const didClickOnToggleEl = e.composedPath().includes(this.toggleEl)
    const didClickOnContentEl = e.composedPath().includes(this.contentEl)

    if (didClickOnToggleEl) {
      // Already handled in local click handler
      return
    }

    if (didClickOnContentEl) {
      // Keep open
      return
    }

    // Clicked somewhere else on window
    this.close()
  }

  private getBackdropZIndex() {
    const zIndex = 50000 - 3 // $zindex-navigation

    switch (this.level) {
      case "meta":
        return `${zIndex + 2}` // overlap "main" and "meta"

      case "main":
        return `${zIndex + 1}` // overlap "meta"

      default:
        // "meta"
        return `${zIndex}`
    }
  }

  public componentDidLoad() {
    anime.set(this.contentEl!, { display: "none" })
  }

  private onToggleClick(): void {
    if (!hasSlot(this.el, "content")) {
      return
    }

    this.toggle()
  }

  private getHostClassNames() {
    return {
      scrollable: this.scrollable,
      [getAppearance(this.el)]: true,
      [this.layout]: true,
      [this.level]: true,
    }
  }

  private getToggleClassNames() {
    return {
      toggle: true,
      arrow: hasSlot(this.el, "content"),
      [this.display]: true,
    }
  }

  private getContentClassNames() {
    return {
      content: true,
    }
  }

  public render() {
    return (
      <Host class={this.getHostClassNames()}>
        {/* eslint-disable-next-line jsx-a11y/no-static-element-interactions */}
        <span
          class={this.getToggleClassNames()}
          onClick={() => this.onToggleClick()}
          ref={(el) => (this.toggleEl = el)}
        >
          <slot name="toggle" />
        </span>

        <div
          class={this.getContentClassNames()}
          style={{ width: this.width }}
          ref={(el) => (this.contentEl = el)}
        >
          <sdx-trap-focus
            ref={(el) => (this.trapFocusEl = el)}
            lang={this.el.lang}
          >
            <nav ref={(el) => (this.slotContainerEl = el)} data-nosnippet>
              <slot name="content" />
            </nav>
          </sdx-trap-focus>
        </div>

        <sdx-backdrop
          ref={(el) => (this.sdxBackdropEl = el)}
          style={{ zIndex: this.getBackdropZIndex() }}
        />
      </Host>
    )
  }
}
