import { Component, h, Element, Prop } from "@stencil/core"
import { createItemForAccordion } from "../helpers"
import { Item } from "../types"
import { getAppearance } from "../../../core/helpers/webcomponent-helpers"

/**
 * @private
 */
@Component({
  tag: "sdx-header-left-hand-menu",
  styleUrl: "../header.scss",
  shadow: true,
})
export class HeaderLeftHandMenu {
  @Element() public el!: HTMLSdxHeaderLeftHandMenuElement

  @Prop() public items: Item[] = []
  @Prop() public allItems: Item[] = []
  @Prop() public activeItems: Item[] = []

  private getComponentClassNames() {
    return {
      [getAppearance(this.el)]: true,
      component: true,
    }
  }

  public render() {
    return (
      <nav class={this.getComponentClassNames()}>
        <ul class="ul vertical accordion left-hand-menu">
          {this.items.map((item) =>
            createItemForAccordion(
              item,
              this.allItems,
              this.activeItems,
              "left",
              {
                body: { marginLeft: "23px" }, // $baseline-3 -1px fine adjustment
                arrow: {
                  background:
                    getAppearance(this.el) === "sdx--light-theme"
                      ? "#015" /* $color-navy */
                      : "#adadad" /* $color-gray-dark-theme*/,
                },
              },
              2
            )
          )}
        </ul>
      </nav>
    )
  }
}
