$container-offset: $baseline-3 - ($baseline * 0.5); // make-container-fixed(ul);
$radius: 3px;

sdx-header-menu {
  display: flex;
  height: 100%;
  position: relative; // for .content positioning

  &:not(.slot) {
    .content {
      padding: $baseline-2;
    }
  }

  // Scrolling can be disabled by the consumers which allows to "append"
  // divs using `position: absolute;` which collides with `overflow: scroll`.
  // For example the search results in Portal.
  &.scrollable {
    .content {
      @include scroll-y;
    }
  }

  &.main,
  &.meta {
    .content {
      top: calc(100% + 1px); // +1px border
    }
  }

  &.meta {
    .toggle.arrow {
      // add the .arrow
      a.link {
        @include arrow($color-navy);
        @include arrow--down;
        padding-right: 21px; // spacing for arrow

        &::before {
          left: auto;
          right: 6px; // 6px from mixins/_arrow.scss
        }

        &::after {
          left: auto;
          right: 0;
        }

        &:hover {
          &::before,
          &::after {
            background: $color-int-blue--active;
          }
        }
      }

      &.open,
      &.opening {
        a.link {
          @include arrow--up;

          &:not(:hover) {
            &::before,
            &::after {
              background: $color-int-blue;
            }
          }
        }
      }
    }

    .content {
      top: 100%;
    }
  }

  &.auto {
    .content {
      padding-top: 0;
      padding-bottom: $baseline;
    }
  }

  &.fixed-left,
  &.fixed-right {
    .content {
      width: 100%;

      @include bp-sm {
        width: 360px; // smallest mobile width found on statcounter.com
      }

      // Allow long links to break within the fixed width menus
      a.link {
        white-space: normal;
      }
    }
  }

  &.fixed-right {
    .content {
      right: 0;
      margin-right: -$baseline-2;
    }
  }

  .toggle {
    display: flex;
    cursor: pointer;

    &.open,
    &.opening {
      a.link:not(:hover) {
        color: $color-int-blue;
      }
    }
  }

  .content {
    position: absolute;
    background: $color-white;
    margin-left: -$baseline-2;
    border-radius: 0 0 $border-radius-large $border-radius-large;
    border: 1px solid $color-aluminium;
    border-top: 0;
    display: none;
  }
}

sdx-header-menu.sdx--dark-theme {
  &.meta {
    .toggle.arrow {
      a.link {
        &::before,
        &::after {
          background: $color-int-gray-dark-theme;
        }

        &:hover {
          &::before,
          &::after {
            background: $color-int-blue2-dark-theme--active;
          }
        }
      }

      &.open,
      &.opening {
        a.link {
          &:not(:hover) {
            &::before,
            &::after {
              background: $color-int-blue2-dark-theme;
            }
          }
        }
      }
    }
  }

  .toggle {
    &.open,
    &.opening {
      a.link:not(:hover) {
        color: $color-int-blue2-dark-theme;
      }
    }
  }

  .content {
    background: $color-dusk-tint-2-dark-theme;
    border-color: $color-gray-tint-7-dark-theme;
  }
}
